.headertext {
    display: flex;
    position: absolute;
    left: 50vw;
    color: #FFFFFF;
}

.sticky {
    display: flex;
    position: fixed;
    width: 100vw;
    padding-left: 50vw;
    color: #000000;
    background-color: rgba(255, 255, 255, 0.98);
    -webkit-box-shadow: 0 2px 2px #efefef;
    box-shadow: 0 2px 2px #efefef;
    z-index: 9999;
}

.sidebar {
    display: none;
    position: fixed;
    width: 100vw;
    padding-left: 80vw;
    color: #000000;
    background-color: rgba(255, 255, 255, 0.98);
    -webkit-box-shadow: 0 2px 2px #efefef;
    box-shadow: 0 2px 2px #efefef;
    z-index: 9999;
}

@media screen and (max-width: 1024px) {
    .headertext {
        display: flex;
        position: absolute;
        left: 30vw;
        font-size: 2vw
    }

    .sticky {
        display: flex;
        position: fixed;
        left: 0vw;
        padding-left: 30vw;
        font-size: 2vw;
        color: #000000;
        /* background-color: rgba(255, 255, 255, 0.98); */
        -webkit-box-shadow: 0 2px 2px #efefef;
        box-shadow: 0 2px 2px #efefef;
        z-index: 9999;
        width: 100vw
    }
}

@media screen and (max-width: 425px) {

    .sticky {
        display: none
    }

    .sidebar {
        /* display: inline; */
        /* position: absolute;
        margin-top: auto;
        margin-bottom: auto; */
        display: flex;
        /* align-items: center */
    }
}